<script>
import { getListWithoutToken as getNewsList } from '@/apiUrl/news'
import { getListWithoutToken as getActivityList } from '@/apiUrl/activity'
import { getList as getTechnologyList } from '@/apiUrl/technology'
import { getList as getScienceResultList } from '@/apiUrl/scienceResult'
import { Swiper, SwiperItem, ScrollView, Toast } from 'mand-mobile'
import simple from 'mand-mobile/components/swiper/demo/data/simple'
import { mapGetters } from 'vuex'

export default {
  name: 'swiper-demo',
  components: {
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [Toast.name]: Toast
  },
  computed: {
    ...mapGetters([
      'status'
    ])
  },
  data () {
    return {
      simple,
      newsList: [],
      activityList: [],
      technologyList: [],
      scienceResultList: []
    }
  },
  mounted () {
    console.log('登录状态：' + this.status)
    window.triggerSwiper3 = () => {
      this.goto()
    }
    this.getNewsList()
    this.getActivityList()
    this.getTechnologyList()
    this.getScienceResultList()
  },
  methods: {
    getNewsList () {
      const args = {
        typeId: 1,
        pageSize: 3,
        pageNumber: 1
      }
      getNewsList(args).then(res => {
        this.newsList = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    getActivityList () {
      const args = {
        pageSize: 2,
        pageNumber: 1
      }
      getActivityList(args).then(res => {
        this.activityList = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    getTechnologyList () {
      const args = {
        pageSize: 3,
        pageNumber: 1
      }
      getTechnologyList(args).then(res => {
        this.technologyList = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    getScienceResultList () {
      const args = {
        pageSize: 2,
        pageNumber: 1
      }
      getScienceResultList(args).then(res => {
        this.scienceResultList = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 跳转新闻详情页
    $_onNewsItemClick (i) {
      this.$router.push({
        path: '/news/detail',
        query: { id: i.id }
      })
    },
    // 跳转活动详情页
    $_onActivityItemClick (i) {
      this.$router.push({
        path: '/activity/detail',
        query: { id: i.id }
      })
    },
    // 跳转技术需求详情页
    $_onTechnologyItemClick (i) {
      this.$router.push({
        path: '/technology/detail',
        query: { id: i.id }
      })
    },
    // 跳转科技成果详情页
    $_onScienceResultItemClick (i) {
      this.$router.push({
        path: '/scienceResult/detail',
        query: { id: i.id }
      })
    },
    // 跳转到发布页面
    turnToPublish (i) {
      this.$router.push({
        path: '/scienceResult/detail',
        query: { id: i.id }
      })
    },
    // 轮播图相关方法
    setValue (id, value) {
      document.querySelector(id) && (document.querySelector(id).innerHTML = value)
    },
    beforeChange (from, to) {
      this.setValue('#valueSwiper10', from)
      this.setValue('#valueSwiper11', to)
    },
    afterChange (from, to) {
      this.setValue('#valueSwiper12', from)
      this.setValue('#valueSwiper13', to)
    },
    goto () {
      this.$refs.swiper.goto(2)
    },
    // 新闻资讯跳转
    turn_news () {
      this.$router.push('/news')
    },
    turn_publish () {
      this.$router.push('/publish/index')
    }
  }
}
</script>

